.meta-section {
    background: $color-white-1;
    display: flex;
    justify-content: flex-start;
    position: relative;
    z-index: 0;
    @media (max-width: $lg) {
        height: auto !important;
        justify-content: flex-start;
        flex-direction: column;
    }
    .fp-overflow {
        padding: 12rem 0 8rem;
        @media (max-width: $lg) {
            min-height: 100vh;
            padding-top: 8rem;
            display: flex;
            flex-direction: column;
            overflow-x: hidden;
        }
    }
    &__bg {
        height: calc(100% - 24rem);
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        position: absolute;
        right: 0;
        bottom: 0;
        user-select: none;
        pointer-events: none;
        z-index: -1;
        @media (max-width: $lg) {
            height: auto;
            max-height: 9999px;
            margin: -8rem -9rem -.2rem var(--gap);
            padding-top: 0;
            position: relative;
        }
        img,
        svg {
            width: auto;
            height: 100%;
            display: block;
            @media (max-width: $lg) {
                width: 100%;
                height: auto;
            }
        }
        &-dots,
        &-line {
            position: absolute;
            top: 0;
            right: 0;
            clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
        }
        &-dots {
            z-index: 1;
        }
    }
    &__container {
        flex-shrink: 0;
        @media (max-width: $lg) {
            padding-top: 8rem;
            padding-bottom: 8rem;
        }
    }
    &__title {
    	max-width: 67%;
    	margin: 0 0 8vh;
        @media (max-width: $lg) {
            max-width: 80rem;
            margin: 0 auto 8rem;
            text-align: center;
        }
        @media (max-width: $md) {
            max-width: 50rem;
        }
    	span {
    		color: $color-gray-1;
    	}
    }
}

.fp-viewing-meta .meta-section__bg-line,
.fp-viewing-meta .meta-section__bg-dots {
    animation: meta-road 5s;
    animation-fill-mode: forwards;
}

@media (max-width: $lg) {
    .js-scroll-trigger.is-visible .meta-section__bg-line,
    .js-scroll-trigger.is-visible .meta-section__bg-dots {
        animation: meta-road 5s;
        animation-fill-mode: forwards;
    }
}

@keyframes meta-road {
    0% {
        clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
    }
    100% {
        clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0% 100%);
    }
}